import { useEffect, useState, useReducer } from "react";
import Carousel from "react-bootstrap/Carousel";
import { reducer } from "../../common/CartReducer";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { ADDTOCART, SETGRANDTOTAL } from "../../redux/cartSlice";
/*
import one from "../../assets/images/1.png";
import two from "../../assets/images/2.png";
import three from "../../assets/images/3.png"; */
function MiddleCarousel(Products: any) {
  //console.log("Product", Products);
  const [index, setIndex] = useState(0);

  const dispatch = useDispatch();
  // const [cartState, dispatch] = useReducer(reducer, cart);
  const navigate = useNavigate();
  // console.log("products typeof", typeof Products);
  // console.log("products", Products.props);
  const handleSelect = (Products: any) => {
    //setIndex(selectedIndex);
    //  console.log("Products", Products);
    navigate(`/product-details/${Products.id}`, { state: Products });
  };
  const chunkSize = 4;
  let newArray: any = [];
  for (let i = 0; i < Products.props.length; i += chunkSize) {
    newArray.push(Products.props.slice(i, i + chunkSize));
    //  console.log("chunk", newArray);
    // do whatever
  }
  const addToCart = (item: any) => {
    let cart = {
      ...item,
      quantity: 1,
      date: new Date().toString(),
      totalPrice: item.price,
      deliveryDate: new Date().toString(),
      deliveryOption: "R",
      expressDeliveryDate: new Date().toString(),
    };
    dispatch(ADDTOCART(cart));

    dispatch(SETGRANDTOTAL());
  };
  useEffect(() => {
    //carousel.pause();
  }, []);
  return (
    <div className="row   middle-section">
      <div className="position-relative topic mt-5 mb-5">
        <p className="">{Products.title}</p>
        <hr />
      </div>
      <Carousel
        activeIndex={index}
        interval={null}
        indicators={false}
        data-bs-interval="false"
      >
        {newArray.map((item: any, i: any) => {
          {
            {
              return (
                <Carousel.Item key={i + "a"} className="mx-auto">
                  {item.map((_item: any, j: any) => {
                    return (
                      <div key={j + "a"} className="col-3  d-inline-block">
                        <div className="card" style={{ width: "20rem" }}>
                          <img
                            src={_item.src}
                            className="card-img-top"
                            alt={_item.name}
                          />
                          <div className="card-body">
                            <h5 className="card-title">{_item.name}</h5>
                            <p
                              className="card-text d-inline-block text-truncate"
                              style={{ maxWidth: "250px" }}
                            >
                              {_item.description}
                            </p>
                            <a
                              className=" btn btn-light fw-bold "
                              onClick={() => handleSelect(_item)}
                            >
                              VIEW DETAILS
                            </a>
                            <a
                              className="btn btn-light fw-bold"
                              onClick={() => addToCart(_item)}
                            >
                              ADD TO CART
                            </a>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </Carousel.Item>
              );
            }
          }
        })}
        {/*  {Products.props.map((img: any, i: any) => {
        return (
          <Carousel.Item key={i}>
            {" "}
            <img src={img.src} />
          </Carousel.Item>
          _item
        );
      })} 
       
                       {_item.map((img: any, i: any) => {
                  return (
                    <Carousel.Item key={i}>
                      {_item[i][0]}
                      {_item.toString()}
                    </Carousel.Item>
                  );
                })}
       
       */}
      </Carousel>
    </div>
  );
}

export default MiddleCarousel;
