import { createSlice } from "@reduxjs/toolkit";
import { Console } from "console";
import { stat } from "fs";

export const cartSlice = createSlice({
  name: "cart",
  initialState: {
    cartItemList: [
      {
        id: 0,
        name: "",
        src: "./assets/images/doughnut/1.jpg",
        description: "Sinful Sugar glaze on Hevenly doughnut",
        price: 20,
        starRating: 5,
        category: ["doughnuts", "sweet", "Sinful Sugar"],
        quantity: 0,
        orderDate: new Date().toString(),
        totalPrice: 0,
        deliveryDate: new Date().toString(),
        deliveryOption: "R",
        expressDeliveryDate: new Date().toString(),
      },
    ],
    grandTotal: 0,
  },
  reducers: {
    ADDTOCART: (state, action) => {
      if (state?.cartItemList[0]?.id === 0) {
        //  console.log("state in redux0", JSON.stringify(state));
        state.cartItemList = [action.payload];
      } else {
        //console.log("state in redux0", JSON.stringify(state));
        state.cartItemList.push(action.payload);
      }
      // SETGRANDTOTAL();
      //  console.log("Slice redux addtocart:::", JSON.stringify(state));
    },
    UPDATECART: (state, action) => {
      let j: number = action.payload.j;

      if (state.cartItemList[j].deliveryOption == "E") {
        state.cartItemList[j].quantity = action.payload.value;
        state.cartItemList[j].totalPrice =
          state.cartItemList[j].price * action.payload.value + 10;
      }
      if (state.cartItemList[j].deliveryOption == "R") {
        state.cartItemList[j].quantity = action.payload.value;
        state.cartItemList[j].totalPrice =
          state.cartItemList[j].price * action.payload.value;
      }
      //SETGRANDTOTAL();
    },
    REMOVEITEM: (state, action) => {
      //console.log("payload hia bhai", action.payload);
      state.cartItemList.splice(action.payload, 1);
      //SETGRANDTOTAL();
      //   state.value += action.payload;
    },
    SETGRANDTOTAL: (state) => {
      let _grandTotal = state.cartItemList.reduce((accum, item) => {
        return (accum += item.totalPrice);
      }, 0);
      state.grandTotal = _grandTotal;
      //  console.log("grandTotal hia bhai", state.grandTotal);
    },
    GETDELIVERYDATE: (state, action) => {
      var someDate = new Date();
      let j: number = action.payload.index;
      var numberOfDaysToAdd = 6;
      var numberOfDaysToAddExpress = 1;
      var result = someDate.setDate(someDate.getDate() + 6);
      var resultExpress = someDate.setDate(someDate.getDate() + 4);
      state.cartItemList[action.payload.index].deliveryDate = new Date(
        resultExpress
      ).toDateString();

      state.cartItemList[action.payload.index].expressDeliveryDate = new Date(
        result
      ).toDateString();
      state.cartItemList[action.payload.index].deliveryOption =
        action.payload.d;
    },

    UPDATEDELIVERYOPTION: (state, action) => {
      let j: number = action.payload.j;
      // console.log("action hia bhai123 DO", action.payload);
      // let do: string = action.payload.DO;
      state.cartItemList[j].deliveryOption = action.payload.DilOpt;

      if (action.payload.DilOpt == "E") {
        state.cartItemList[j].totalPrice =
          state.cartItemList[j].price * state.cartItemList[j].quantity + 10;
      }
      if (action.payload.DilOpt == "R") {
        state.cartItemList[j].totalPrice =
          state.cartItemList[j].price * state.cartItemList[j].quantity;
      }

      //SETGRANDTOTAL();
    },
  },
});

// Action creators are generated for each case reducer function
export const {
  ADDTOCART,
  UPDATECART,
  REMOVEITEM,
  SETGRANDTOTAL,
  UPDATEDELIVERYOPTION,
  GETDELIVERYDATE,
} = cartSlice.actions;

export default cartSlice.reducer;
