import React, { useEffect, useState, useContext, useReducer } from "react";
import { useLocation, useParams } from "react-router-dom";
import { ADDTOCART, SETGRANDTOTAL } from "../redux/cartSlice";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import {
  imagesCupcakesSingle,
  imagesDoughnutSingle,
  imagesCakesSingle,
} from "../constants/constants";
function ProductsPage() {
  const { state } = useLocation();
  const navigate = useNavigate();

  const dispatch = useDispatch();

  const allItems = [
    ...imagesCupcakesSingle,
    ...imagesDoughnutSingle,
    ...imagesCakesSingle,
  ];
  const [selected, setSelected] = useState(allItems);
  const handleSelect = (Products: any) => {
    //setIndex(selectedIndex);
    //  console.log("Products", Products);
    navigate(`/product-details/${Products.id}`, { state: Products });
  };
  const handleSearchItems = (selectedItem: string) => {
    // console.log("here");
    let _selectedProducts: any = [];
    // console.log("selected", selected);
    allItems.map((element: any) => {
      //  console.log("element", element);
      // selected.map((item: any) => {
      // console.log("here", element.name.includes(item));
      if (element.category.includes(selectedItem.toLowerCase())) {
        _selectedProducts.push(element);
      }
      // console.log("_selected", _selectedProducts);
      setSelected(_selectedProducts);
    });
    setSelected(_selectedProducts);

    // console.log("selected", selectedProducts);
  };
  const addToCart = (item: any) => {
    let cart = {
      ...item,
      quantity: 1,
      date: new Date().toString(),
      totalPrice: item.price,
      deliveryDate: new Date().toString(),
      deliveryOption: "R",
      expressDeliveryDate: new Date().toString(),
    };
    dispatch(ADDTOCART(cart));

    dispatch(SETGRANDTOTAL());
  };
  return (
    <>
      <div>ProductsPage</div>
      <div className="row" id="productlistpage">
        <div className="col-2 m-3">
          <nav
            id="navbar-example3"
            className="navbar navbar-light bg-light flex-column align-items-stretch"
          >
            <a className="navbar-brand" href="#">
              <h3>Categories</h3>
            </a>
          </nav>{" "}
          <div id="list-example" className="list-group">
            <nav className="nav nav-pills flex-column">
              {" "}
              <a
                className="list-group-item list-group-item-action"
                onClick={() => handleSearchItems("CUPCAKES")}
              >
                CUPCAKES
              </a>
              <a
                className="list-group-item list-group-item-action"
                onClick={() => handleSearchItems("cakes")}
              >
                CUSTOM CAKES
              </a>
              <nav className="nav nav-pills flex-column">
                <a
                  className="list-group-item list-group-item-action"
                  onClick={() => handleSearchItems("Kids Cakes")}
                >
                  Kids Cakes
                </a>
                <a
                  className="list-group-item list-group-item-action"
                  onClick={() => handleSearchItems("girls")}
                >
                  Girls Cakes
                </a>
              </nav>
              <a
                className="list-group-item list-group-item-action"
                onClick={() => handleSearchItems("DOUGHNUTS")}
              >
                DOUGHNUTS
              </a>
              <a
                className="list-group-item list-group-item-action"
                onClick={() => handleSearchItems("bagels")}
              >
                BEGALS AND BUNS
              </a>
            </nav>
          </div>
        </div>
        <div className="col-9">
          {" "}
          <div
            data-bs-spy="scroll"
            data-bs-target="#navbar-example3"
            data-bs-offset="0"
          >
            <h4 id="item-1">Item 1</h4>
            <div className="container mt-3" id="search-page">
              <div className="row  justify-content-center pt-3">
                {" "}
                {selected.map((_item: any, j: any) => {
                  return (
                    <div key={j + "a"} className="col-3  d-flex my-2 mb-5">
                      <div className="card" style={{ width: "18rem" }}>
                        <img
                          src={_item.src}
                          className="card-img-top img-fluid rounded-start"
                          alt={_item.name}
                        />
                        <div className="card-body">
                          <h5 className="card-title">{_item.name}</h5>
                          <p
                            className="card-text d-inline-block text-truncate"
                            style={{ maxWidth: "250px" }}
                          >
                            {_item.description}
                          </p>
                          <a
                            className=" btn btn-light fw-bold "
                            onClick={() => handleSelect(_item)}
                          >
                            VIEW DETAILS
                          </a>
                          <a
                            className="btn btn-light fw-bold ml-5"
                            onClick={() => addToCart(_item)}
                          >
                            ADD TO CART
                          </a>
                        </div>
                      </div>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>{" "}
        </div>
      </div>
    </>
  );
}

export default ProductsPage;
