import "./App.scss";
import "bootstrap-icons/font/bootstrap-icons.css";
import React, {
  useContext,
  useEffect,
  useReducer,
  useState,
  useRef,
} from "react";

import { useNavigate } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "react-bootstrap-typeahead/css/Typeahead.css";
import { useLocation } from "react-router-dom";
import { NavLink } from "react-router-dom";
import { Outlet } from "react-router-dom";
import CartPage from "./pages/cart";
import store from "./redux/store";
import {
  imagesCupcakesSingle,
  imagesDoughnutSingle,
  imagesCakesSingle,
} from "./constants/constants";
import { Provider } from "react-redux";
import { Typeahead } from "react-bootstrap-typeahead"; // ES2015
function App() {
  const { pathname } = useLocation();
  const typeaheadRef: any = useRef(null);
  const allItems = [
    ...imagesCupcakesSingle,
    ...imagesDoughnutSingle,
    ...imagesCakesSingle,
  ];
  const [selected, setSelected] = useState([]);
  const [selectedProducts, setSelectedProducts] = useState([]);
  const navigate = useNavigate();
  // const cartvalue = useContext(CartContext);
  let cartVal = 0;
  var options: string[] = [];
  allItems.map((_item: any) => {
    //console.log("heree sercah opt", _item);
    if (!options.includes(_item.name) && _item.name !== undefined) {
      options.push(_item.name);
    }
    _item.category.forEach((element: string) => {
      if (!options.includes(element) && element !== undefined) {
        options.push(element);
      }
    });

    //  console.log("final options", options);
  });
  const [showCart, setShowCart] = useState(true);
  useEffect(() => {
    cartVal = store.getState().cart.cartItemList.length;
    // console.log("cartVal", cartVal);
  }, [store.getState().cart.cartItemList]);
  // Log the state whenever it changes
  // console.log("Counter state:", cartState);
  const handleSearchItems = () => {
    // console.log("here");
    let _selectedProducts: any = [];
    // console.log("selected", selected);
    allItems.map((element: any) => {
      //  console.log("element", element);
      selected.map((item: any) => {
        // console.log("here", element.name.includes(item));
        if (element.name.includes(item)) {
          _selectedProducts.push(element);
        }
        if (element.category.includes(item)) {
          _selectedProducts.push(element);
        }
        if (element.description.includes(item)) {
          _selectedProducts.push(element);
        }
        // console.log("_selected", _selectedProducts);
        setSelectedProducts(_selectedProducts);
      });
    });
    setSelectedProducts(_selectedProducts);
    navigate(`/search-results`, {
      state: { products: _selectedProducts, serachCriteria: selected },
    });
    // console.log("selected", selectedProducts);
  };
  return (
    <Provider store={store}>
      <div className="App">
        <header className="App-header text-bg-dark">
          <a
            href="/"
            className="d-flex align-items-center my-2 my-lg-0 me-lg-auto text-white text-decoration-none"
          >
            <img src="../../../assets/images/LOGO.png" className="logo" />
          </a>
          <Typeahead
            multiple
            id="keep-menu-open"
            onChange={(selected: any) => {
              setSelected(selected);
              // Keep the menu open when making multiple selections.
              typeaheadRef.current.toggleMenu();
            }}
            options={options}
            placeholder="Search..."
            ref={typeaheadRef}
            selected={selected}
          >
            <i
              className="bi bi-search cursor-pointer"
              style={{ cursor: "pointer" }}
              onClick={handleSearchItems}
            ></i>
          </Typeahead>
          <ul className="nav col-12 col-lg-auto my-2 justify-content-center my-md-0 text-small">
            <li>
              <NavLink
                to="/"
                className={({ isActive, isPending }) =>
                  isPending
                    ? "pending nav-link text-white"
                    : isActive
                    ? "active nav-link text-white text-active"
                    : " nav-link text-white"
                }
              >
                <i className="bi bi-house" style={{ color: "white" }}></i>
                Home
              </NavLink>
            </li>
            <li>
              <NavLink
                to="/cart"
                className={({ isActive, isPending }) =>
                  isPending
                    ? "pending nav-link text-white"
                    : isActive
                    ? "active nav-link text-white text-active"
                    : " nav-link text-white"
                }
              >
                <i className="bi bi-cart" style={{ color: "white" }}></i>
                Cart
              </NavLink>
            </li>

            <li>
              <NavLink
                to="/products"
                className={({ isActive, isPending }) =>
                  isPending
                    ? "pending nav-link text-white"
                    : isActive
                    ? "active nav-link text-white text-active"
                    : " nav-link text-white"
                }
              >
                <i
                  className="bi bi-cake2-fill text-white"
                  style={{ color: "white" }}
                ></i>
                Products
              </NavLink>
            </li>
            {/*  <li>
              <NavLink
                to="/messages"
                className={({ isActive, isPending }) =>
                  isPending
                    ? "pending nav-link text-white"
                    : isActive
                    ? "active nav-link text-white text-active"
                    : " nav-link text-white"
                }
              >
                <i
                  className="bi bi-file-person text-white"
                  style={{ color: "white" }}
                ></i>
                Profile
              </NavLink>
            </li> */}
          </ul>
        </header>
        <Outlet />
        {pathname.includes("cart") === true ? (
          ""
        ) : (
          <>
            {" "}
            {showCart ? (
              <div
                id="cart-side"
                className="position-absolute top-0  end-0"
                style={{ zIndex: "9999999999999999900", right: "0px" }}
              >
                <div
                  className="rounded bg-white float-end p-2 position-absolute "
                  style={{ zIndex: "9999999999999999901", right: "0px" }}
                  onClick={() => setShowCart(false)}
                >
                  <i className="bi bi-x-lg "></i>
                </div>
                <CartPage />
              </div>
            ) : (
              <>
                <div
                  id="cart-side-hidden"
                  className="rounded bg-white position-fixed  end-0 p-2"
                  style={{ top: "60px" }}
                  onClick={() => setShowCart(true)}
                >
                  {" "}
                  <div className="cart-size">
                    {store.getState().cart.cartItemList.length}
                  </div>
                  &nbsp;<i className="bi bi-cart"></i>
                </div>
              </>
            )}
          </>
        )}
      </div>
    </Provider>
  );
}

export default App;
